import { APP_BASE_HREF, LocationStrategy } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-body',
  templateUrl: './body.component.html',
  styleUrls: ['./body.component.css']
})
export class BodyComponent implements OnInit {

  started: boolean = false;
  stop: boolean = false;
  pause: boolean = false;

  dataactual: string = "";
  horaactual: string = "";

  times: number[] = [5, 10, 15, 20, 25, 30, 40];
  selected = 20;
  useSelected: boolean = true;

  display: string = "";

  alarm = new Audio();

  constructor() {}

  ngOnInit(): void {
    this.alarm.src = "./assets/sounds/alarm.mp3";
  }

  public mostraHora(): void{
    var date, seconds, minutes, hour, dd, mm, yyyy, mdyhms;
    setInterval(() => {
        date = new Date();
        seconds =  String(date.getSeconds()).padStart(2, '0');
        minutes = String(date.getMinutes()).padStart(2, '0');
        hour = String(date.getHours()).padStart(2, '0');
        dd = String(date.getDate()).padStart(2, '0');
        mm = String(date.getMonth() + 1).padStart(2, '0'); //January is 0!
        yyyy = date.getFullYear();
    
        var mdy = mm + '/' + dd + '/' + yyyy;
        var hms = hour + ':' + minutes + ':' + seconds
        this.dataactual = mdy;
        this.horaactual = hms;
    }, 1000);
  }

  startTimer(duration: number): void{
    if(this.started == false){
      this.started = true;
      this.stop = false;
      var timer = duration, m, s;
      var intervalId = setInterval(() =>{
        if(!this.pause){
          if (--timer < 0) {
            this.started = false;
            timer = this.selected*60;
            this.playAlarm();
            clearInterval(intervalId);
          }
          if(this.stop){
            this.stop = false;
            this.started = false;
            timer = this.selected*60;
            clearInterval(intervalId);
          }
          m = parseInt(String(timer / 60), 10);
          s = parseInt(String(timer % 60), 10);

          m = m < 10 ? "0" + m : m;
          s = s < 10 ? "0" + s : s;

          this.display = m + ":" + s;
        }
      }, 1000);
    }
  }

  public mostraCount(): void{
    if(!this.pause){
      this.display = this.selected + ":00";
    }
    if(this.useSelected){
      this.useSelected = false;
    }
    this.stop = false;
    this.pause = false;
    var numberOfMinutes = 60*this.selected;
    this.startTimer(numberOfMinutes);
  }

  public pausaCount(): void{
    this.pause = true;
  }

  public aturaCount(): void{
    this.pause = false;
    this.stop = true;
    this.display = this.selected + ":00"
  }

  playAlarm(): void{
    this.alarm.play();
  }
}