import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Eyesight Care';

  isMobile = this.isUserMobile();

  isUserMobile(): boolean{
    if( screen.width <= 760 ) {
        return true;
    }
    else {
        return false;
    }
  }
}
